@import url(https://fonts.googleapis.com/css?family=Courier+Prime|Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.App {
  text-align: center;
}

.App-link {
  color: #09d3ac;
}

h1{
  font-family: 'Press Start 2P', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img.bg {
  min-width: 100%;
  min-width: 1024px;

  width: 100%;
  height: auto;

  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

.aboutGame {
  font-family: 'Courier Prime', monospace;
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
}

.homeText{
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
}

.storyText{
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 25px;
  margin-top: 25%;
  padding: 2%;
  margin-bottom: 20px;
}

.nameText{
  color: white;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 16px;
}

.bg{
  background-image: url(/junglegame-react/App.js/static/media/jungle.3d7c283d.jpg);
  background-size: cover;
  min-height: 1000px;
  z-index:-1;

} 

.bgrow{
  background-image: url(/junglegame-react/App.js/static/media/jungle.3d7c283d.jpg);
  background-size: cover;
  
} 

.imageCorner {
  height: 70px;
  width: 60;
  border: 2px white solid;
  border-radius: 50px;
  margin: 10px;
}


.animBtn:hover {
  -webkit-animation: stretch 1.8s;
          animation: stretch 1.8s;
}
@-webkit-keyframes stretch{
  25%{
    -webkit-transform: scale(1.3, 0.6);
            transform: scale(1.3, 0.6);
  }
  35%{
    -webkit-transform: scale(0.6, 1.3);
            transform: scale(0.6, 1.3);
  }
  50%{
    -webkit-transform: scale(1.1, 0.9);
            transform: scale(1.1, 0.9);
  }
  65%{
    -webkit-transform: scale(0.9, 1.02);
            transform: scale(0.9, 1.02);
  }
}
@keyframes stretch{
  25%{
    -webkit-transform: scale(1.3, 0.6);
            transform: scale(1.3, 0.6);
  }
  35%{
    -webkit-transform: scale(0.6, 1.3);
            transform: scale(0.6, 1.3);
  }
  50%{
    -webkit-transform: scale(1.1, 0.9);
            transform: scale(1.1, 0.9);
  }
  65%{
    -webkit-transform: scale(0.9, 1.02);
            transform: scale(0.9, 1.02);
  }
}

.animCard:hover {
  -webkit-animation: stretch 2s;
          animation: stretch 2s;
}
@keyframes stretch{
  25%{
    -webkit-transform: scale(1.1, 0.9);
            transform: scale(1.1, 0.9);
  }
  35%{
    -webkit-transform: scale(0.9, 1.02);
            transform: scale(0.9, 1.02);
  }
  50%{
    -webkit-transform: scale(1.1, 0.9);
            transform: scale(1.1, 0.9);
  }
  65%{
    -webkit-transform: scale(0.9, 1.02);
            transform: scale(0.9, 1.02);
  }
}


.page {
  position: absolute;
  left: 0;
  right: 0;
}

.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active{
  opacity: 1;
  -webkit-transition: opacity 500ms linear 500ms;
  transition: opacity 500ms linear 500ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 500ms linear 500ms;
  transition: opacity 500ms linear 500ms;
}

.pageout-enter{
  opacity: 0;
  z-index: 1;
}

.pageout-enter-active{
  opacity: 1;
  -webkit-transition: opacity 1500ms linear 500ms;
  transition: opacity 1500ms linear 500ms;
}

.pageout-exit{
  opacity: 1;
}

.pageout-exit-active{
  opacity: 0;
  -webkit-transition: opacity 1500ms linear 500ms;
  transition: opacity 1500ms linear 500ms;
}

.homeBtn{
  margin-top: 3%;
  margin-left: 90%;
}

